body {
    font-family: 'Roboto', sans-serif;
    line-height: 1.4;
}

.container{
    max-width: 960px;
    margin: auto;
    overflow: hidden;
    padding: 0 3rem;
}



#showcase {

    color: #fff;
    height: 100vh;
    position: relative;
}

#showcase::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}

#showcase .showcase-content {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    height: 100%;
}

#showcase h1{
    font-size: 4rem;
}

#showcase p {
    font-size: 1.3rem;
}

.bttn{
    display: inline-block;
    border: none;
    background: rgb(56, 17, 117);
    color: #fff;
    padding: 0.75rem 1.5rem;
    margin-top: 1rem;
    transition: opacity 1s ease-out;  
    text-decoration: none;
}

.bttn:hover {
    opacity: 0.5;
}

.App {
  display: flex;
  width: 100%;
  align-items: stretch;
}


/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
.sidebar {
  min-width: 250px;
  max-width: 250px;
  background: rgb(34, 94, 143);
  color: #fff;
  margin-left: -10px;
  
}



.sidebar-header {
  background: rgb(52, 130, 194);
}

.sidebar-header h3 {
  color: #fff;
  padding: 1em;
}

.sidebar ul p {
  color: #fff;
  padding: 10px;
}


.sidebar-header > span {
  position: relative;
  float: right;
  margin: 0.5em;
  font-size: 2rem;
  cursor: pointer;
  display: none;
}
.side-menu {
  height: calc(110vh - 130px);
  overflow-y: scroll;
}

.side-menu::-webkit-scrollbar {
  width: 10px;
}



.navlink{
    color: inherit !important;
    -webkit-text-decoration-color: ivory;
            text-decoration-color: ivory;
}

.navitem :hover{
    background-color: #306ca3;
    opacity: 80%;
}

fieldset {
  padding: 1em;
  font:80%/1 sans-serif;
  }
.jumbotron-billboard .img {
    margin-bottom: 0px;
    opacity: 0.2;
    color: #fff;
    background: #000 url("https://bootstrapcreative.com/wp-bc/wp-content/uploads/2017/03/learning-story.png") top center no-repeat;
    width: 100%;
    height: 100%;
    background-size: cover;
    overflow: hidden;
    position:absolute;
    top:0;
    left:0;
    z-index:1;
}
.jumbotron h2 {margin-top:0;}
.jumbotron {
  position:relative;
  padding-top:50px;
  padding-bottom:50px;
}
.jumbotron .container {
  position:relative;
  z-index:2;
}

@media screen and (max-width: 768px) {
  .jumbotron {
    padding-top:20px;
    padding-bottom:20px;
  }
}

.idcard{
  position: relative;
